<template>
  <el-card class="">
    <div slot="header"><span>编辑管理员</span></div>

    <el-form ref="form" :model="form" :rules="rules" label-width="80px">
      <el-form-item label="用户名" prop="username">
        <el-input v-model.trim="form.username"></el-input>
      </el-form-item>
      <el-form-item label="姓名" prop="fullname">
        <el-input v-model.trim="form.fullname"></el-input>
      </el-form-item>
      <el-form-item label="角色">
        <el-select v-model="form.role_id" :disabled="user_id == 1" placeholder="请选择">
          <el-option v-for="role in roles" :key="role.role_id" :label="role.role_name" :value="role.role_id">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="性别" prop="sex">
        <el-radio-group v-model="form.sex">
          <el-radio label="男"></el-radio>
          <el-radio label="女"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="手机" prop="tel">
        <el-input v-model.trim="form.tel"></el-input>
      </el-form-item>
      <el-form-item label="邮箱" prop="email">
        <el-input v-model.trim="form.email"></el-input>
      </el-form-item>
      <el-form-item label="头像" prop="avatar">
        <single-upload v-model="form.avatar" :data="{ type: 'avatar' }"
                       :default-image="`${$server}/images/avatar/default.jpg`"
                       :remove-action="`${$server}/upload/remove/`"
                       :upload-action="`${$server}/upload/common/`"
        >
        </single-upload>
      </el-form-item>
      <el-form-item>
        <el-button @click="handleEdit" size="medium" type="primary">保存修改</el-button>
      </el-form-item>
    </el-form>

  </el-card>
</template>

<script>
import { Admin, Role } from '@/api/';
import SingleUpload from '@/components/SingleUpload.vue';
import { mapState } from "vuex";

export default {
  props: ['user_id'],
  components: {
    SingleUpload,
  },
  data() {
    return {
      form: {
        username: '',
        fullname: '',
        role: '',
        sex: '男',
        tel: '',
        email: '',
        avatar: '',
      },
      role_options: [],
      rules: {
        username: [
          { type: 'string', required: true, message: '请输入账号！', trigger: 'blur' },
          { min: 3, max: 20, message: '账户名长度在 3 到 20 个字符', trigger: 'blur' },
        ],
        fullname: [{ type: 'string', required: true, message: '请输入姓名！', trigger: 'blur' }],
        sex: [{ required: true, message: '请选择性别', trigger: 'change' }],
        tel: [
          { type: 'string', required: true, message: '请输入手机号码', trigger: 'blur' },
          {
            type: 'string',
            pattern: /^[1][3|4|5|6|7|8|9][0-9]{9}$/,
            message: '请输入正确的手机号码',
            trigger: 'blur'
          },
        ],
        email: [{ type: 'email', required: true, message: '请输入邮箱地址', trigger: 'blur' }],
        avatar: [{ type: 'string', required: true, message: '请上传头像图片', trigger: 'change' }],
      },
    };
  },
  created() {
    document.title = "编辑管理员";
    // 初始化，获取账户信息
    this.load_profile();
  },
  watch: {
    "$route": "load_profile",
  },
  computed: {
    ...mapState('Role', ['roles']),
  },
  methods: {
    // 初始化
    async load_profile() {
      // 加载管理员资料
      let { status: profile_status, data: profile } = await Admin.info({ user_id: this.user_id });
      if (profile_status) {
        // 还原表单
        this.form = profile;
      }
    },
    handleEdit() {
      this.$refs.form.validate(async valid => {
        if (valid) {
          let { status, msg } = await Admin.edit({ user_id: this.user_id, ...this.form });
          if (status) {
            // 信息提示
            this.$message.success(msg);
            // 跳转页面
            this.$router.back();
          } else {
            // 信息提示
            this.$message.error(msg);
          }
        }
      });
    },
  },
};

</script>

<style></style>
